@import "../../data/styles.css";

.homepage{
    display: flex;
    flex-direction: column;
    background-color: var(--primary-color);
    color: var(--accent-green);
}

.homepage-container {
    display: flex;
    align-items: center;
    min-height: 300px;
}

.homepage-left-container,
.homepage-right-container {
    padding: 10px;

}

.homepage-right-container{
    width: 30%;
}

.homepage-left-container {
    width: 70%;
    /* border-right: 2px solid rgb(246, 240, 240); */
    flex-direction: row;
    justify-content: center;
}

.homepage-left-item,
.homepage-right-item {
    margin: 10px;    
}

.homepage-profile-picture{
    padding-top: 7px;	
    display: flex;
	height: 300px;
    border-radius: 13%;
    transform: rotate(-3deg);
}


.homepage-after-title {
	display: flex;
}

.homepage-works {
	flex-basis: 600px;
	padding-top: 30px;
}

.homepage-projects {
	flex-basis: 600px;
	padding-top: 40px;
    margin-left: 3%;
}

.homepage-socials {
	display: flex;
	justify-content: right;
	align-items: center;
	padding-top: 30px;
}

.homepage-social-icon {
	padding-right: 20px;
	font-size: 25px;
	transition: color 0.2s ease-in-out;
	color: var(--link-color);
}

.homepage-social-icon:hover {
	color: var(--accent-orange);
}

@media (max-width: 800px){
    .homepage-container{
        display: flex;
        flex-direction: column;
    }

    .homepage-right-container, .homepage-left-container{
        width: 80%;
        border-right: 0px solid rgb(246, 240, 240);
    }
    
    .homepage-after-title{
        flex-direction: column;
    }
}
