.tanstaafl-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 50px;
    padding: 20px;
    border-radius: 10px;
    background-color: #f8f9fa; /* Subtle background */
    color: #212529; /* Dark text for contrast */
  }
  
  .tanstaafl-title {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 10px;
    color: #333;
    letter-spacing: 0.2rem;
  }
  
  .tanstaafl-meaning {
    font-size: 1.2rem;
    font-style: italic;
    color: #555;
  }
  