.all-projects-container {
	display: flex;
	flex-wrap: wrap;
	align-items: stretch;
	padding-top: 30px;
	padding-bottom: 30px;
	margin-left: -35px;
	margin-right: -35px;
	/* width: 50px; */
}

.all-projects-project {
	width: calc(100% / 2);
	box-sizing: border-box;
	padding: 10px;
	/* overflow: hidden; */
}

@media (max-width: 600px) {
	.all-projects-project {
		width: calc(100% / 1);
		box-sizing: border-box;
		padding: 10px;
		overflow: hidden;;
	}
	.all-projects-container{
		width: 100%;
	}
}
