

.projects-content-wrapper{
	background-color: var(--primary-color);
    color: var(--accent-green);
}

.title.projects-title{
	width: 100%;
	color: var(--accent-green);
}
.subtitle.projects-subtitle{
	width: 100%;
	color: var(--accent-green);
}

.projects-container {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 20px;
	padding: 50px;
}

@media (max-width: 600px) {
	.projects-container {
		padding-top: 60px;
		padding: 0px;
	}

	.projects-project {
		width: calc(100% / 2);
	}
}
