.panda{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 15px;
    z-index:0;
}

.head{
    position: relative;
    width: 50px;
    height: 50px;
    border: 1px solid #000;
    background: #fff;
    border-radius: 80% 80% 60% 60%;
}

.ear{
    position: absolute;
    width: 23px;
    height: 23px;
    background: #000;
    border-radius: 50%;
    z-index: -1;
}

.ear.left{
    top:-5px;
    left:-5px;
}

.ear.right{
    top:-5px;
    right:-5px;
    transform-origin: 0% 100%;
    animation: ear 5s cubic-bezier(0.06, 0.63, 0.91, 0.66) 2s infinite;
}

.eye{
    position: absolute;
    width: 15px;
    height: 19px;
    background: #000;
    border-radius: 50%;
}

.eye.left{
    top:13px;
    left:8px;
    border-radius: 100% 60% 80% 60%;
}

.eye.right{
    top:13px;
    right:8px;
    border-radius: 60% 100% 60% 80%;
}

.pupil{
    position: absolute;
    top:8px;
    left:4px;
    width:5px;
    height: 5px;
    background: #fff;
    border-radius: 50%;
}

.dark-pupil{
    position: absolute;
    top:11px;
    left:3px;
    width:7px;
    height: 7px;
    background: #000;
    border-radius: 50%;
}

.nose{
    position: absolute;
    top:60%;
    left:20px;
    width: 10px;
    height: 7px;
    background-color: #000;
    border-radius: 50%;
}

.mouth{
    position: absolute;
    top:40%;
    left:26%;
    width: 20px;
    height: 20px;
    /* background: #000; */
    border: 2px solid transparent;
    border-bottom: 2px solid #000;
    border-radius: 0 0 30% 30%;
}

@keyframes blink {
    0%, 90%, 100% {
        height: 15px; /* Normal eye height */
        transform: rotate(180deg);

    }
    95% {
        height:0px; /* Blinking effect */
    }
    100%{
        height: 0px;
    }

}

.pupil {
    animation: blink 5s infinite;
}

@keyframes ear{
    20%{ transform: rotate(10deg);}
    21%{ transform: rotate(-5deg);}
    22%{transform: rotate(5deg);}
}

.panda:hover {
    .mouth{
    border-radius: 0 0 100% 100%;
    }
}