.spotify-playlist-container {
    padding: 0px;
    color: #fff;
    text-align: center;
  }
  .spotify-playlist-container h3 {
    font-size: 1.2rem;
    margin-bottom: 10px;
  }
  iframe {
    border: none;
    border-radius: 5%;
  }

  .spotify-hide-show-button {
    background: linear-gradient(90deg, #ff7e5f, #feb47b); /* Gradient colors */
    border: none;
    border-radius: 20px;
    color: #fff;
    font-size: 0.7rem;
    font-weight: bold;
    padding: 10px 20px;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
  }
  
  .spotify-hide-show-button:hover {
    background: linear-gradient(90deg, #feb47b, #ff7e5f); /* Reverse gradient */
    transform: scale(1.05); /* Slight grow effect */
    box-shadow: 0px 6px 8px rgba(0, 0, 0, 0.3);
  }
  
  .spotify-hide-show-button:active {
    transform: scale(0.95); /* Slight shrink on click */
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  }

