.logo-container {
	display: flex;
	justify-content: left;
	position: relative;
	z-index: 999;
	padding-top: 25px;
    padding-left: 25px;
}

.logo {
	display: flex;
	position: fixed;
	border: 1px solid white;
	border-radius: 50%;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
	top: 4vh;
}

a:hover{
	.mouth{
		border-radius: 0 0 100% 100%;
		}
}