:root {
	/* ------- colors ------- */
	--primary-color: rgb(229, 249, 252); /* Light Blue */
	--secondary-color: #a6b1b3; /* Medium Gray */
	--tertiary-color: #acacb4; /* Light Gray */
	--quaternary-color: #f9f9fb; /* Background Light Gray */
	--link-color: #c57aff; /* Soft Lavender Purple */
	--text-color: #efe9e9; /* Light Gray */
	--accent-green: #6af9b6; /* Vivid Green */
	--accent-orange: #ff7e5f; /* Bright Orange */
	/* ---------------------- */

	/* ------- fonts ------- */
	--primary-font: "Heebo", sans-serif;
	--secondary-font: "Roboto", sans-serif;

	@font-face {
		font-family: 'MatrixCodeNFI';
		src: url('/public/fonts/matrix-code-nfi.ttf') format('truetype');
	}
	/* --------------------- */
}

@media (prefers-color-scheme: dark) {	
	:root {
		/* ------- colors ------- */
		--primary-color: #1e1e28; /* Deep Gray */
		--secondary-color: #2a2a38; /* Dark Gray */
		--tertiary-color: #f7f9fa; /* Off White */
		--quaternary-color: #010111; /* Dark Purple */
		--link-color: #c57aff; /* Soft Lavender Purple */
		--text-color: #efe9e9; /* Light Gray */
		--accent-green: #6af9b6; /* Vivid Green */
		--accent-orange: #ff7e5f; /* Bright Orange */
		/* ---------------------- */
	}
}
