@import "../../../data/styles.css";

.project {
	mix-blend-mode: normal;
	opacity: 0.5;
	height: 100%;
}

.project a {
	text-decoration: none;
}

.project:hover {
	opacity: 1;
	transition: background-color 0.3s ease-in-out;
}

.project:hover .project-link {
	transition: color 0.3s ease-in-out;
}

.project-link:hover{
	color: var(--link-color);
}

.project-container {
	padding: 30px;
	margin-right: 20px;
	border-radius: 20px;
	background-color: var(--primary-color);
    color: var(--accent-green);
	height: 80%;
}

.project-logo {
	width: 30px;
}

.project-logo img {
	width: 100%;
}

.project-title {
	font-family: var(--secondary-font);
	/* color: var(--primary-color); */
	font-size: 16px;
	padding-top: 15px;
	margin-bottom: 1em;
	font-weight: 700;
}

.project-description {
	/* color: var(--secondary-color); */
}

.project-link {
	display: flex;
	align-items: center;
	/* color: var(--secondary-color); */
	font-size: 12px;
	padding-top: 20px;
}

.project-link-icon {
	padding-left: 5px;
	font-size: 13px;
}

.project-link-text {
	padding-left: 20px;
	font-weight: 700;
}

@media (max-width: 600px) {
	.project {
		height: 100%;
		width: 100%;
	}
}
